
body {
  padding: 0;
  margin: 0;
}
ul {
  padding: 0;
  margin: 0;
}
ul li {
  list-style: none;
}
p {
  margin: 0 0 12px 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  background-color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#app .resume {
  margin-top: 40px;
  position: relative;
  width: 790px;
  border: 1px solid #dad8d7;
  background-color: white;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app .resume .left {
  width: 240px;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float: left;
  background-color: #fdfefe;
  position: relative;
}
#app .resume .right {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: calc(100% - 240px);
  float: left;
  padding: 25px 20px;
  border-left: 1px solid #dad8d7;
}
#app .resume .right p {
  font-size: 14px;
  line-height: 18px;
  color: #555;
  text-align: justify;
}
#app .actions {
  position: absolute;
  top: 40px;
  left: 50%;
  margin-left: 415px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
#app .actions button {
  padding: 5px 8px;
  border-radius: 4px;
  background-color: white;
  border: 1px solid #dad8d7;
  margin-bottom: 5px;
  cursor: pointer;
}
#app .actions button:hover {
  background-color: #f8f8f8;
}
#app .guide {
  border: 1px solid #dad8d7;
  width: 790px;
  margin-top: 15px;
  height: 80px;
}
#app .guide ul {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#app .guide ul li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#app .guide ul li img {
  margin: 0 10px;
}
#app .guide ul li h5 {
  margin: 0 0 6px 0;
}
#app .guide ul li p {
  margin: 0;
  font-size: 12px;
}
#app .footer {
  line-height: 50px;
}
#app .footer a {
  text-decoration: none;
  color: #4aa74f;
}
#app .footer a:first-child {
  display: block;
  text-align: center;
  line-height: 0;
  margin-top: 15px;
}
@page {
  size: auto;
  margin: 0cm;
  color: #ffffff;
}
@media print {
html,
  body,
  #app {
    font-size: 0 !important;
    min-height: auto !important;
    height: auto !important;
}
* {
    color: #000 !important;
    overflow: hidden !important;
}
.resume {
    margin-top: 0!important;
}
.actions,
  .guide,
  .footer {
    display: none!important;
}
}



